#myProgress {
    width: 100%;
    background-color: mediumturquoise;
}

@import url('https://fonts.googleapis.com/css?family=Lato');

#myBar {
    width: 0%;
    height: 3px;
    background-color: rgb(0, 0, 0);
}
.login-page {
    height:275px;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: center;
}
.login-field{
    height:60px;
    margin-top:20px;
    display:flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

.generate-pass{
    display: flex;
    width: 300px;
    height: 250px;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}
.generate-pass__password{
    width: 200px;
}
.checkboxes{
    width: 250px;
    display:flex;
    flex-direction: column;
    align-items: center;
}
.length-slider{
    width:250px;
    display:flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}
.pass-slider{
    width: 100px
}

.save-pass{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}
.get-pass{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.hidden {
    display: none;
}
.home {
    position: relative;
    width: 300px;
    height: 30px;
}
.home-button {
    text-align: center;
    font-size: medium;
    cursor: pointer;
    font-weight: 500;
    background-color: transparent;
    border: 0ch;
    text-align: center;
    display: flex;
    align-items: center;
}

.popup-content {
    height: 400px;
    width: 300px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}
.popup-content__fields{
    height:100px;
    width: auto;
    display: flex;
    gap: 10px;
    flex-direction: column;
    justify-content: center;
    align-items: left;
}
.popup-content__fields--label{
    font-size: large;
    font-weight: 500;
}
.popup-content__heading{
    text-align: center;
    margin-bottom: 20px;
    color: rgb(80, 80, 80);
}
.popup-content__buttons{
    height: 100px;
    width: 230px;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.action{
    margin: 5px 0;
    width: 100px;
    height: 30px;
    border: 1px solid black;
    border-radius: 5px;
    text-align: center;
    font-size: medium;
    cursor: pointer;
    font-weight: 500;

}
.pass-display{
    display:none;
}
.back-button{
    display:none;
}



* {
  box-sizing: border-box;
}

body {
  font-family: 'Lato', sans-serif;
  
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  transition: background 0.4s linear;

  text-align: center;
  margin: 0;
  min-height: 100vh;
}

h2 {
  color: #fff;
  position: absolute;
  top: 20px;
}

.tab-nav-container {
    background-color: #fff;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
    display: flex;
    justify-content: space-between;
    padding: 30px;
    width: 450px;
}

.tab {
    background-color: #ffffff;
    border-radius: 50px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 20px;
    margin: 0 10px;
    transition: background 0.4s linear;
}

.tab i {
    font-size: 1.2em;
}

.tab p {
    font-weight: bold;
    /*overflow: hidden;*/
    max-width: 200px;
}

.tab.active p {
    /* margin-left: 10px; */
    max-width: 200px;
    transition: max-width 0.4s linear;
}

.tab.active.purple {
    background-color: rgba(91, 55, 183, 0.2);
    color: rgba(91, 55, 183, 1);
}

.tab.active.pink {
    background-color: rgba(201, 55, 157, 0.2);
    color: rgba(201, 55, 157, 1);
}

.tab.active.yellow {
    background-color: rgba(230, 169, 25, 0.2);
    color: rgba(230, 169, 25, 1);
}

.tab.active.teal {
    background-color: rgba(28, 150, 162, 0.2);
    color: rgba(28, 150, 162, 1);
}

@media (max-width: 450px) {
    .tab-nav-container {
        padding: 20px;
        width: 300px;
    }

    .tab {
        padding: 0 10px;
        margin: 0;
    }

    .tab i {
        font-size: 1em;
    }
}
